* {
  font-family: 'Karla', sans-serif;
  font-size: 62.5%;
}

.App {
  display: flex;
  flex-direction: column;
}

body {
  background: #b5caf9ff;
  /* height: 100vh; */
}

header {
  position: sticky;
  background: wheat;
  font-weight: bold;
  display: flex;
  justify-content: space-around;
  font-size: 3rem;
  padding: 1rem;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.1);  
  /* box-shadow: 2px 2px 2px tan; */
}

header div {
  text-align: center;
}

section {
  font-size: 4rem;
  font-weight: bold;
  margin: 0.5em;
}

.jobCards {
  margin: 0.5em;
  display: flex;
  overflow-x: auto;
  flex-wrap: nowrap;
}

.job--card {
  text-align: center;
  font-size: 2.4rem;
  padding: 2em;
  margin: 1rem;
  background: whitesmoke;
  border-radius: 5px;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.1);  
  display: flex;
  flex-direction: column;
  flex: 1 1 50vw;
}

.company {
  font-size: 3rem;
  font-weight: bold;
}

.role {
  font-size: 2rem;
  font-style: italic;
}

footer {
  width: 100%;
  padding: 2rem;
  font-weight: bold;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  font-size: 3rem;
  bottom: 0px;
  background: wheat;
  padding: 2rem;
  /* box-shadow: 2px -2px 2px tan; */
  box-shadow: 0px -3px 8px rgba(0, 0, 0, 0.1);  
  /* position: absolute; */
}

.email {
  color: black;
  text-decoration: none;
}